.responsive-info {
  // display: none !important
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  background: black;
  color: white;
  padding: 3px 10px;
  font-size: 12px;
  .info {
    display: none; }
  @include mobile {
    .mobile {
      display: block; } }
  @include tablet-only {
    .tablet {
      display: block; } }
  @include desktop-only {
    .desktop {
      display: block; } }
  @include widescreen-only {
    .widescreen {
      display: block; } }
  @include fullhd {
    .fullhd {
      display: block; } } }


